<template>
    <scroll-view scroll-y='true' class="content" @scroll='scrollMain' @scrolltolower='nextList'>
        <!-- heads -->
        <heads :sTop='scrollTop' :title='"好物推荐"'></heads>

        <!-- 公告 -->
        <view class="notice">
            <image src="@/static/icon/announcement.png"></image>
            <u-notice-bar :font-size='20' :volume-icon='false' type='none' mode="vertical" :list="nList"></u-notice-bar>
        </view>

        <!-- tab -->
        <view class="tabView">
            <tTabs :tabList='tList'></tTabs>
        </view>

        <view class="header">
            <!-- swiper -->
            <swiper class="index_swiper">
                <swiper-item v-for="(item, index) in swiperList" :key="index">
                    <view class="swiper-item">
                        <image :src="item"></image>
                    </view>
                </swiper-item>
            </swiper>
        </view>

        <view class="goodList">
            <u-waterfall v-model="flowList" ref="uWaterfall">
                <template v-slot:left="{leftList}">
                    <view class="demo-warter pLeft" v-for="(item, index) in leftList" :key="index">
                        <view class="demo-warter-hide">
                            <u-lazy-load threshold="-450" :image="item.image"
                                :index="index"></u-lazy-load>
                        </view>
                        <view class="demo-title">
                            {{item.title}}
                        </view>
                        <view class="demo-shop" v-if="item.shop">
                            {{item.shop}}
                        </view>
                        <view class="demo-price">
                            <text class="red">￥</text>
                            <text class="red price">{{item.price}}</text>
                            <text class="orPrice">￥{{item.price}}</text>
                        </view>
                    </view>
                </template>
                <template v-slot:right="{rightList}">
                    <view class="demo-warter pRight" v-for="(item, index) in rightList" :key="index">
                        <view class="demo-warter-hide">
                            <u-lazy-load threshold="-450" :image="item.image"
                                :index="index"></u-lazy-load>
                        </view>
                        <view class="demo-title">
                            {{item.title}}
                        </view>
                        <view class="demo-shop" v-if="item.shop">
                            {{item.shop}}
                        </view>
                        <view class="demo-price">
                            <text class="red">￥</text>
                            <text class="red price">{{item.price}}</text>
                            <text class="orPrice">￥{{item.price}}</text>
                        </view>
                    </view>
                </template>
            </u-waterfall>
            <u-loadmore bg-color="rgb(240, 240, 240)" :status="loadStatus" @loadmore="addRandomData"></u-loadmore>
        </view>

        <!-- tabbar -->
        <u-tabbar :list="tabbar" :mid-button="true" height='58px' active-color='#FFD3AA'></u-tabbar>
    </scroll-view>
</template>

<script>
    export default {
        data() {
            return {
                swiperList: ['https://cdn.uviewui.com/uview/swiper/1.jpg'],
                scrollTop: 0,
                nList: [
                    '寒雨连江夜入吴',
                    '平明送客楚山孤',
                    '洛阳亲友如相问',
                    '一片冰心在玉壶'
                ],
                tList: [{
                        tName: '唱片',
                    },
                    {
                        tName: '影音',
                    },
                    {
                        tName: '图书',
                    },
                    {
                        tName: '周边',
                    },
                    {
                        tName: '生活馆',
                    }
                ],
                loadStatus: 'loadmore',
                flowList: [],
                list: [{
                        price: 35,
                        title: '北国风光，千里冰封，万里雪飘',
                        shop: 'CD',
                        image: 'http://pic.sc.chinaz.com/Files/pic/pic9/202002/zzpic23327_s.jpg',
                    },
                    {
                        price: 75,
                        title: '望长城内外，惟余莽莽',
                        image: 'http://pic.sc.chinaz.com/Files/pic/pic9/202002/zzpic23325_s.jpg',
                    },
                    {
                        price: 385,
                        title: '大河上下，顿失滔滔',
                        image: 'http://pic2.sc.chinaz.com/Files/pic/pic9/202002/hpic2119_s.jpg',
                    },
                    {
                        price: 784,
                        title: '欲与天公试比高',
                        image: 'http://pic2.sc.chinaz.com/Files/pic/pic9/202002/zzpic23369_s.jpg',
                    },
                    {
                        price: 7891,
                        title: '须晴日，看红装素裹，分外妖娆',
                        image: 'http://pic2.sc.chinaz.com/Files/pic/pic9/202002/hpic2130_s.jpg',
                    },
                    {
                        price: 2341,
                        title: '江山如此多娇，引无数英雄竞折腰',
                        shop: 'CD',
                        image: 'http://pic1.sc.chinaz.com/Files/pic/pic9/202002/zzpic23346_s.jpg',
                    },
                    {
                        price: 661,
                        title: '惜秦皇汉武，略输文采',
                        image: 'http://pic1.sc.chinaz.com/Files/pic/pic9/202002/zzpic23344_s.jpg',
                    },
                    {
                        price: 1654,
                        title: '唐宗宋祖，稍逊风骚',
                        image: 'http://pic1.sc.chinaz.com/Files/pic/pic9/202002/zzpic23343_s.jpg',
                    },
                    {
                        price: 1678,
                        title: '一代天骄，成吉思汗',
                        image: 'http://pic1.sc.chinaz.com/Files/pic/pic9/202002/zzpic23343_s.jpg',
                    },
                    {
                        price: 924,
                        title: '只识弯弓射大雕',
                        image: 'http://pic1.sc.chinaz.com/Files/pic/pic9/202002/zzpic23343_s.jpg',
                    },
                    {
                        price: 8243,
                        title: '俱往矣，数风流人物，还看今朝',
                        image: 'http://pic1.sc.chinaz.com/Files/pic/pic9/202002/zzpic23343_s.jpg',
                    },
                ]
            }
        },
        onLoad() {
            this.addRandomData();
        },
        computed: {
            tabbar: function () {
                return getApp().globalData.tabbar
            }
        },
        methods: {
            scrollMain(e) {
                this.scrollTop = e.detail.scrollTop
            },
            nextList() {
                this.loadStatus = 'loading';
                // 模拟数据加载
                setTimeout(() => {
                    this.addRandomData();
                    this.loadStatus = 'loadmore';
                }, 1000)
            },
            addRandomData() {
                for (let i = 0; i < 10; i++) {
                    let index = this.$u.random(0, this.list.length - 1);
                    // 先转成字符串再转成对象，避免数组对象引用导致数据混乱
                    let item = JSON.parse(JSON.stringify(this.list[index]))
                    item.id = this.$u.guid();
                    this.flowList.push(item);
                }
            },
        }
    }
</script>

<style lang="scss" scoped>
    .notice {
        position: relative;
        width: 686rpx;
        background: #FFFFFF;
        border-radius: 60rpx 60rpx 60rpx 60rpx;
        margin: 28rpx auto 0;

        /deep/.u-notice-bar {
            margin-left: 50rpx;
        }

        image {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 20rpx;
            margin: auto;
            width: 40rpx;
            height: 40rpx;
        }
    }

    .content {
        width: 100vw;
        height: 100vh;
        overflow: auto;
        background: url('https://image-file2024.oss-cn-beijing.aliyuncs.com/geWoLiMusicMall/backgroundImage/%E6%AD%8C%E7%AA%9D%E9%87%8C%E8%83%8C%E6%99%AF%E5%9B%BE1.png');
        background-size: 100% 100%;
    }

    .header {

        .index_swiper {
            width: 686rpx;
            margin: 20rpx auto 0;
            border-radius: 20rpx;
            overflow: hidden;
            height: 304rpx;

            image {
                width: 686rpx;
                height: 304rpx;
            }
        }
    }

    .tabView {
        width: 686rpx;
        margin: 30rpx auto 0;
        color: #999;
        display: flex;
    }

    .goodList{
        width: 686rpx;
        margin: 34rpx auto;
    }
    .demo-warter {
        .demo-warter-hide{
            border-radius: 16rpx 16rpx 0 0 ;
            overflow: hidden;
        }
        margin: 20px 5rpx;
        background-color: #ffffff;
        // padding: 16rpx;
        border-radius: 16rpx;
        position: relative;
        overflow: hidden;
    }
    
    .pLeft{
        margin-right: 21rpx;
    }
    .pRight{
        margin-left: 21rpx;
    }
    
    .u-close {
        position: absolute;
        top: 32rpx;
        right: 32rpx;
    }

    .demo-image {
        width: 100%;
        border-radius: 4px;
    }

    .demo-title {
        font-size: 24rpx;
        margin-top: 5px;
        padding: 0 16rpx;
        color: #000;
    }

    .demo-price {
        font-size: 30rpx;
        color: $u-type-error;
        margin-top: 5px;
        padding: 0 16rpx;
        display: flex;
        align-items:  center;
        .red{
            color: #FB3758;
            font-size: 24rpx;
            font-weight: 500;
        }
        .price{
            font-size: 28rpx;
            font-weight: 800;
        }
        .orPrice{
            margin-left: auto;
            font-size: 20rpx;
            color: rgba(153,153,153,0.6);
            text-decoration: line-through;
        }
    }

    .demo-shop {
        font-size: 20rpx;
        padding: 0 16rpx;
        color: #999999;
        margin-top: 5px;
    }
</style>